<template>
  <div class="userEvaluation-box">
    <div class="separate"></div>
    <div class="srtop">
      买家评价
      <span>{{total}}</span>
      <div class="sale" v-if="flag">
        | 销量
        <span>12339</span>
      </div>
      
    </div>
    <div class="tags" v-if="rateTags.length!=0">
      <span v-for="(rt, i) in rateTags" :key="'rt'+i" :class="rt.emotion=='positive'?'tagPositive':'tagNegative'">{{rt.value}}({{rt.num}})</span>
    </div>
    <div class="comt" v-if="commentsList.length==0">暂无评价</div>
    <div class="ctInfo">
      <div class="singlect" v-for="(cs, i) in commentsList" :key="'cs'+i">
        <div class="userPic">
          <img :src="cs.user.avatar" alt="">
        </div>
        <div class="userName"><span>{{cs.user.uname}}</span></div>
        
        <div class="ctcontent">{{cs.content}}</div>
        <div class="cttime">{{cs.time}}</div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        flag:false
      }
    },
    props:{
      commentsList:{type:Array,default:[]},
      rateTags:{type:Array,default:[]},
      total:""
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .userEvaluation{
    &-box{
      .separate{
        height:20px;/* px */
        background:@separateColor;
      }
      .srtop{
        height:70px;/* px */
        line-height: 70px;/* px */
        color:@oldPriceColor;
        padding-left:40px;/* px */
        .sale{
          display: inline-block;
        }
       
      }
      .comt{
        padding-left:40px;/* px */
        color:@oldPriceColor;
        font-size: 30px;/* px */
      }
      .tags{
        display: flex;
        padding-left:40px;/* px */
        padding-right:40px;/* px */
        flex-wrap: wrap;
        .tagPositive{
          color:@color;
          background:@limitBgColor;
          padding:10px 10px;/* px */
          margin-right:10px;/* px */
          margin-bottom:10px;/* px */
        }
        .tagNegative{
          color:@font-color;
          background:@bodyBgColor;
          padding:10px 10px;/* px */
          margin-right:10px;/* px */
          margin-bottom:10px;/* px */
        }
      }
      .ctInfo{
        padding:0 40px;/* px */
        >div:not(:last-child){
          border-bottom: @border @bottomBorder;/* px */
        }
        .singlect{
          margin-top:40px;/* px */
          .userPic{
            width:64px;/* px */
            height:64px;/* px */
            line-height: 64px;/* px */;
            border-radius: 50%;/* px */
            overflow: hidden;
            border:@border @userPicBorderColor;/* no */
            display: inline-block;
            vertical-align: top;
            img{
              width:100%;
              height:auto;
            }
          }
          .userName{
            height:64px;/* px */
            line-height: 64px;/* px */;
            display: inline-block;
            margin-left:10px;/* px */
            font-size: 28px;/* px */
            color:@titleColor;
          }
          .ctcontent{
            padding:20px 0px;/* px */
            font-size: 26px;/* px */
            color:@commentColor;
          }
          .cttime{
            padding:0 0px;/* px */
            color:@oldPriceColor;
            padding-bottom:40px;/* px */
          }
        }
      }
    }
  }
</style>


