import api from '../../service/api';

const state={
  classifyLeftList:[],
  RightClassifyList:[],
  sortFilter:[],
  productionList:[],
  fcid:"",
};
const actions={
  loadLeftClassify({commit}){
    api.loadDatasByPids(117330,(data)=>{
        commit("setclassifyLeftList",data.data[117330].list);
    });
  },
  loadRightClassify({commit},pid=3627){
    commit("setRightClassifyList",[]);
    api.loadDatasAboutClassify({pid,appPlat:"m"},(data)=>{
      commit("setRightClassifyList",data.data.list);
    });
  },
  loadRightClassifyProduction({commit},params={page:1,sort:"pop",fcid:10062603}){
    params.cKey = "h5-cube";
    let fcid = params.fcid;
    // commit("setproductionList",[]);
    api.loadDatasAboutSearch(params,(data)=>{
      commit("setsortFilter",data.result.sortFilter);
      commit("setproductionList",data.result.wall.docs);
      commit("setFcid",fcid);
    });
  }
  
};
const mutations={
  setclassifyLeftList(state,data){
    state.classifyLeftList=data;
  },
  setRightClassifyList(state,data){
    state.RightClassifyList=data;
  },
  setsortFilter(state,data){
    state.sortFilter=data;
  },
  setproductionList(state,data){
    state.productionList=data;
  },
  setFcid(state,data){
    state.fcid = data;
  }
};

export default {
  state,
  actions,
  mutations
};



