<template>
  <div class="nxdyigate-box">
    <ul>
      <li v-for="(single,i) in nxdyigateList" :key="'single'+i">
        <img :src="single.image" alt="">
        <div>{{single.title}}</div>
      </li>
    </ul>
  </div>
</template>
<script>
  export default{
    props:{
      nxdyigateList:{type:Array,default:[]}
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .nxdyigate{
    &-box{
      ul{
        display: flex;
        flex-wrap:wrap;
        box-sizing: border-box;
        width:100%;
        padding:0;
        margin-top:25px;/* px */
        li{
          flex-grow:1;
          width:20%;
          list-style: none;
          text-align: @align;
          margin-bottom:25px;/* px */
          font-family: @font-family;
          color:@gray;
          img{
            width:70%;
          }
          div{
            font-size:26px;/* px */
          }
        }
      }
    }
  }
</style>


