<template>
    <div class="classify-box">
        <top-nav-bar></top-nav-bar>
        <div class="classifyArea">
            <classify-left class="classify-left" :classifyLeftList="classify.classifyLeftList"></classify-left>
            <classify-right class="classify-right" :RightClassifyList="classify.RightClassifyList" :sortFilter="classify.sortFilter" :productionList="classify.productionList"></classify-right>
        </div>
        <bottom-nav-bar></bottom-nav-bar>
    </div>
</template>
<script>
    import TopNavBar from "../components/TopNavBar";
    import BottomNavBar from "../components/BottomNavBar";
    import ClassifyLeft from '../components/ClassifyLeft';
    import ClassifyRight from '../components/ClassifyRight';
    import api from '../service/api';
    import { mapState,mapActions } from "vuex";
    export default {
        computed:{
            ...mapState(["classify"])
        },
        methods:{
            ...mapActions(["loadLeftClassify","loadRightClassify","loadRightClassifyProduction"])
        },
        components:{
            TopNavBar,BottomNavBar,ClassifyLeft,ClassifyRight
        },
        mounted() {
            this.loadLeftClassify();
            this.loadRightClassify();
            this.loadRightClassifyProduction();
        }
    };
</script>
<style lang="less">
    @import url("../less/less-var.less");
    .classify{
        &-box{
            .classifyArea{
                display: flex;
                .classify-left{
                    flex-grow:1;
                }
                .classify-right{
                    flex-grow:4;
                }
            }
        }
    }
</style>


