<template>
  <div class="carousel-box">
    <swiper class="box-container" :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="(img,i) in listImg" :key="'img'+i">
        <img :src="img.image_800">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
  import 'swiper/dist/css/swiper.css';
  import { swiper, swiperSlide } from 'vue-awesome-swiper';
  export default{
    name: 'carrousel',
    data() {
      return {
        
        swiperOption: {
          initialSlide:1,
          autoplay:1000,
          speed:500,
          loop:true,
          direction:"horizontal",
          pagination:".swiper-pagination",
          autoplayDisableOnInteraction:false
        }
      }
    },
    props:{
      listImg:{type:Array,default:[]}
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    components:{
      swiper,swiperSlide
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .carousel{
    &-box{
      .box-container{
        height:300px;/* px */
        img{
          width:100%;
        }
        .swiper-pagination{
          text-align: left;
          padding:10px;/* px */
          bottom:0;
          .swiper-pagination-bullet{
            width: 12px; /* px */
            height: 12px; /* px */
            background: @white;
            opacity:1;
            letter-spacing: 5px;/* px */
          }
          .swiper-pagination-bullet-active{
            background-color: @pagationColor;
          }
        }
      }
    }
  }
</style>


