import api from '../../service/api';

const state={
  guessLike:[],
  sortFilter:[]
};
const actions={
  loadSearchResult({commit},queryParams={q,sort:"pop"}){
    queryParams.cKey=46;
    queryParams.page=1;
    commit("setguessLike",[]);
    api.loadDatasAboutSearch(queryParams,(data)=>{
      commit("setguessLike",data.result.wall.docs);
      commit("setsortFilter",data.result.sortFilter);
    });
  },
  loadDataByClassify({commit},classifyparams={fcid,title,sort:"pop"}){
    classifyparams.cKey=16;
    classifyparams.page=1;
    commit("setguessLike",[]);
    api.loadDatasAboutSearch(classifyparams,(data)=>{
      commit("setguessLike",data.result.wall.docs);
      commit("setsortFilter",data.result.sortFilter);
    });
  }
};
const mutations={
  setguessLike(state,data){
    state.guessLike=data;
  },
  setsortFilter(state,data){
    state.sortFilter=data;
  }
};

export default {
  state,
  actions,
  mutations
};


