<template>
  <div class="cartTopNavBar-box">
    <i class="iconfont icon-xiangzuojiantou icon-style" @click="changePage()"></i>
    <div class="title">
      购物车(<span>2</span>)
    </div>
    <div class="dowrite">
      编辑
      <i class="iconfont icon-xiaoxizhongxin icon-style"></i>
    </div>
  </div>
</template>
<script>
  export default{
    methods:{
      changePage(){
        this.$router.push("/classfiy");
      }
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .cartTopNavBar{
    &-box{
      height:@topheight - 1; /* px */
      border-bottom:@border @bottomBorder; /* no */
      display: flex;
      line-height: @topheight; /* px */
      text-align:@align;
      font-size: 32px;/* px */
      font-family: @font-family;
      position:fixed;
      top:0;
      left:0;
      right:0;
      i{
        flex-grow:2;
        font-size: 40px;/* px */
      }
      .title{
        flex-grow:5;
      }
      .dowrite{
        flex-grow:1;
        text-align: right;
        padding-right:20px;/* px */
        i{
          font-size: 40px;/* px */
        }
      }
    }
  }
</style>


