import api from '../../service/api';

const state={
  commentsList:[],
  rateTags:[],
  total:""
};
const actions={
  loadComment({commit},itemId){
    commit("setcommentsList",[]);
    commit("setrateTags",[]);
    commit("settotal","");
    api.loadCommentByItemId({appPlat:"m",itemId,type:2},(data)=>{
      if(data.data){
        commit("setcommentsList",data.data.list);
        commit("setrateTags",data.data.rateTags);
        commit("settotal",data.data.total);
      }
    });
  }
};
const mutations={
  setcommentsList(state,data){
    state.commentsList=data;
  },
  setrateTags(state,data){
    state.rateTags=data;
  },
  settotal(state,data){
    state.total=data;
  }
};

export default {
  state,
  actions,
  mutations
};


