<template>
  <div class="prodDetail-box">
    <div class="goback">
        <i class="iconfont icon-xiangzuojiantou" @click="changePage()"></i>
      </div>
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <detail-top :proDetail="proDetail"></detail-top>
        
        <user-evaluation :commentsList="detail.commentsList" :rateTags="detail.rateTags" :total="detail.total"></user-evaluation>
      </div>
    </div>
    <div class="dbnb-box">
      <div class="collect">收藏</div>
      <div class="litstore">小店</div>
      <div class="addCart" @click="addCart()">加入购物车</div>
      <div class="buyNow">立即购买</div>
    </div>
  </div>
</template>
<script>
  import BScroll from "better-scroll";
  import DetailTop from '../components/DetailTop';
  import UserEvaluation from '../components/UserEvaluation';
  import { mapActions, mapState } from "vuex";
  export default{
    data(){
      return{
        proDetail:[],
        cartList:[],
        collectList:{}
      }
    },
    components:{
      DetailTop,UserEvaluation
    },
    computed:{
      ...mapState(["detail"])
    },
    methods:{
      ...mapActions(["loadComment"]),
      getDetailData(){
        let singleGuessLike=JSON.parse(localStorage.getItem(this.$route.query.tradeItemId));
        this.proDetail.push(singleGuessLike);
        this.loadComment(singleGuessLike.tradeItemId);
      },
      changePage(){
        this.$router.go(-1);
        let singleGuessLike=JSON.parse(localStorage.getItem(this.$route.query.tradeItemId));
        localStorage.removeItem(singleGuessLike.tradeItemId);
      },
      addCart(){
        let singleGuessLike=JSON.parse(localStorage.getItem(this.$route.query.tradeItemId));
        console.log(singleGuessLike);
        this.cartList.push(singleGuessLike);
        console.log("-----",this.cartList);
        if(localStorage.getItem("cartList")){
          let oldCartList=localStorage.getItem("cartList").split("-");
          this.cartList=oldCartList.concat(singleGuessLike);
          localStorage.setItem("cartList",this.cartList.join("-"));
          console.log("==========",this.cartList);
        }else{
          localStorage.setItem("cartList",this.cartList.join("-"));
          console.log("==>",this.cartList);
        }
        let c=localStorage.getItem("cartList").split("-");
        console.log(c);
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.wrapper, {
          click:true
        })
      });
      this.getDetailData();
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .prodDetail{
    &-box{
      padding:0;
      margin:0;
      font-family: @font-family;
      .wrapper{
        position: absolute;
        left:0;
        right:0;
        top:@topheight;/* px */
        bottom:@topheight;/* px */
        overflow: hidden;
      }
      .goback{
        height:@topheight;/* px */
        line-height: @topheight;/* px */
        position: fixed;

        i{
          font-size: 50px;/* px */
          margin-left:40px;/* px */
        }
        
      }
      .dbnb-box{
        height:@topheight - 1;/* px */
        line-height: @topheight;
        position: fixed;
        left:0;
        right:0;
        bottom:0;
        border-top:@border @bottomBorder;
        display: flex;
        text-align: @align;
        font-size: 28px;/* px */
        .collect{
          flex-grow:1;
          color:@iconfontcolor;
          border-right:@border @bottomBorder;
        }
        .litstore{
          flex-grow:1;
          color:@iconfontcolor;
        }
        .addCart{
          flex-grow:3;
          background: @limitBgColor;
          color:@color;
        }
        .buyNow{
          flex-grow:3;
          background:@color;
          color:@white;
        }
      }
    }
  }
</style>


