<template>
  <div class="classifyLeft-box">
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <ul class="typeList">
          <li v-for="(ct,i) in classifyLeftList" :class="{active:i==isActive}" :key="'ct'+i" @click="getPid(ct.maitKey,i,ct.miniWallkey)">{{ct.title}}</li>
        </ul>
      </div>
    </div>
    
  </div>
</template>
<script>
  import BScroll from "better-scroll";
  import vuex,{ mapActions } from 'vuex';
  export default{
    data(){
      return{
        pid:"",
        isActive:0,
        fcid:""
      }
    },
    props:{
      classifyLeftList:{type:Array,default:[]}
    },
    methods:{
      ...mapActions(["loadRightClassify","loadRightClassifyProduction"]),
      getPid(pid,i,fcid){
        this.pid=pid;
        this.fcid=fcid;
        this.loadRightClassify(pid);
        this.loadRightClassifyProduction({page:1,sort:"pop",fcid});
        this.isActive=i;
      }
    },
    mounted() {
        this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.wrapper, {
                click:true
            })
        });
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .classifyLeft{
    &-box{
      padding:0;
      margin:0;
      font-family: @font-family;
      background-color: @bodyBgColor;
      .wrapper{
        position: absolute;
        left:0;
        right:75%;/* px */
        top:@topheight;/* px */
        bottom:@topheight;/* px */
        overflow: hidden;
        background-color: @bodyBgColor;
        .content{
          .typeList{
            li{
              height:@topheight;/* 90 */
              color:@iconfontcolor;
              font-size: 28px;
              text-align: @align;
              line-height: @topheight;/* 90 */
            }
            .active{
              color:@color;
              font-weight: bold;
              border-left:@borderTwo @color;
              background:@white;
            }
          }
        }
      }
    }
  }
</style>


