<template>
  <div class="hotType-box">
    <div class="separate"></div>
    <div class="sTitle">热门分类</div>
    <div class="hlist">
      <div class="single" v-for="(l,i) in hotTypesList" :key="'l'+i">
        <div>
          <img :src="l.image" alt="">
        </div>
        <div>{{l.title}}</div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    props:{
      hotTypesList:{type:Array,default:[]}
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .hotType{
    &-box{
      .separate{
        background: @bodyBgColor;
        height:20px;/* px */
      }
      .sTitle{
        text-align: @align;
        background:url("https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png") no-repeat;
        background-size: 85%;
        background-position: 50px 25px;/* px */
        font-size: 30px;/* px */
        padding:15px 0;/* px */
      }
      .hlist{
        display: flex;
        flex-wrap:wrap;
        text-align: center;
        .single{
          flex-grow:1;
          width:25%;
          margin-top:10px;/* px */
          img{
            height:120px;/* px */
          }
        }
      }
    }
  }
</style>


