<template>
    <div class="aboutme-box">
        <div class="wrapper" ref="wrapper">
            <div class="content">
                <div class="top">
                    <div class="userPic">
                        <img src="http://via.placeholder.com/100x100" alt="">
                    </div>
                    <div class="userInfo">
                        <div class="userName">水中的鱼</div>
                        <div class="userDress">我的收获地址
                            <div class="rightArrows"><i class="iconfont icon-xiangzuojiantou"></i></div>
                        </div>
                    </div>
                </div>
                <!-- <div class="separate"></div> -->
                <div class="menu">
                    <div class="item" v-for="(mn,i) in MenuList" :key="'mn'+i">
                        <div class="ico-img">
                            <div class="ico">
                                <img :src="mn.img" alt="">
                            </div>
                        </div>
                        <div class="name">{{mn.name}}</div>
                        <div class="action">
                            <div class="text">{{mn.action}}</div>
                            <div class="Arrows"><i class="iconfont icon-xiangzuojiantou"></i></div>
                        </div>
                        
                    </div>
                    <div class="exit">
                        <input type="button" value="退出登录">
                    </div>
                </div>
            </div>
        </div>
        
        <bottom-nav-bar></bottom-nav-bar>
    </div>
</template>
<script>
    import BScroll from "better-scroll";
    import BottomNavBar from "../components/BottomNavBar";
    export default{
        data(){
            return{
                MenuList:[
                    {img:"../../static/imgs/order.png",name:"我的订单",action:""},
                    {img:"../../static/imgs/cart.png",name:"我的购物车",action:""},
                    {img:"../../static/imgs/group_booking.png",name:"我的拼团",action:""},
                    {img:"../../static/imgs/wallet.png",name:"我的钱包",action:"快速提现"},
                    {img:"../../static/imgs/coupon.png",name:"我的优惠券",action:""},
                    {img:"../../static/imgs/wishlist.png",name:"我收藏的商品",action:""},
                    {img:"../../static/imgs/bookmark.png",name:"我收藏的店铺",action:""},
                    {img:"../../static/imgs/news.png",name:"消息通知",action:""},
                    {img:"../../static/imgs/service.png",name:"客服",action:""},
                    {img:"../../static/imgs/feedback.png",name:"意见反馈",action:""},
                ]
            }
        },
        components:{
            BottomNavBar
        },
        mounted() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.wrapper, {
                click:true
                })
            });
        }
    }
</script>
<style lang="less">
    @import url("../less/less-var.less");
    .aboutme{
        &-box{
            margin:0;
            padding:0;
            background:@bodyBgColor;
            font-family: @font-family;
            .wrapper{
                position: absolute;
                left:0;
                right:0;
                top:0;
                bottom:@topheight;/* px */
                overflow: hidden;
                background:@bodyBgColor;
                .top{
                    background: @white;
                    display: flex;
                    .userPic{
                        width:90px;/* px */
                        height:90px;/* px */
                        border-radius: 50%;
                        overflow: hidden;
                        margin:40px 40px;/* px */
                        margin-right: 0;
                        img{
                            width:100%;
                        }
                    }
                    .userInfo{
                        margin-top:40px;/* px */
                        .userName{
                            font-size: 38px;/* px */
                        }
                        .userDress{
                            font-size: 24px;/* px */
                            margin-top:10px;/* px */
                            color:@titleColor;
                            .rightArrows{
                                display: inline-block;
                                transform: rotateY(180deg);
                                margin-left:40px;/* px */
                                i{
                                    font-size:24px;/* px */
                                }
                            }
                        }
                    }
                }
                /* .separate{
                    height:20px;/* px */
                   /*  background:@bodyBgColor; */
                /* } */
                .menu{
                    >div:first-child{
                        margin-top:20px;/* px */
                        border-bottom:@border @bottomBorder;/* no */
                    }
                    >div:nth-child(2){
                        margin-top:20px;/* px */
                    }
                    >div:nth-child(2),>div:nth-child(3),>div:nth-child(4),>div:nth-child(5),>div:nth-child(6){
                        .name,.action{
                            border-bottom:@border @bottomBorder;/* no */
                        }
                    }
                    >div:nth-child(8){
                        margin-top:20px;/* px */
                        .name,.action{
                            border-bottom:@border @bottomBorder;/* no */
                        }
                    }
                    >div:nth-child(9){
                        .name,.action{
                            border-bottom:@border @bottomBorder;/* no */
                        }
                    }
                    .item{
                        background:@white;
                        height:100px;/* px */
                        line-height: 100px;/* px */
                        display: flex;
                        .ico-img{
                            flex-grow:1;
                            .ico{
                                width:60px;/* px */
                                height:60px;/* px */
                                margin:20px 30px;/* px */
                                img{
                                    width:100%;
                                }
                            }
                        }
                        .name{
                            flex-grow:12;
                            font-size: 30px;/* px */
                        }
                        .action{
                            flex-grow:1;
                            color:@oldPriceColor;
                            .text{
                                display: inline-block;
                                width:100px;/* px */
                            }
                            .Arrows{
                                display: inline-block;
                                transform: rotateY(180deg);
                                text-align: right;
                                width:40px;
                                i{
                                    font-size:24px;/* px */
                                    color:@oldPriceColor;
                                }
                            }
                        }
                    }
                    .exit{
                        text-align: center;
                        margin-top:40px;/* px */
                        padding-bottom:120px;/* px */
                        input{
                            width:600px;/* px */
                            height:80px;/* px */
                            border:none;
                            border-radius:10px;/* px */
                            background: @color;
                            color:white;
                            font-size:30px;/* px */
                        }
                        
                        
                    }
                }
            }
            
        }
        
    }
</style>


