<template>
    <div class="topnavbar-box">
        <div class="search">
            <i v-show="!flag && Flag" class="iconfont icon-xiaoxizhongxin icon-style"></i>
            <i v-show="searchFlag" class="iconfont icon-xiangzuojiantou icon-style" @click="goback()"></i>
            <i v-show="flag" class="iconfont icon-xiangzuojiantou icon-style" @click="getHotSearch()"></i>
            <input class="user-input" type="text" @focus="flag=true;" :placeholder="home.sketch.frontword" v-model="userInput">
            <i class="iconfont icon-sousuo"></i>
            <i v-show="!flag" class="iconfont icon-gouwuche icon-style"></i>
            <span class="sousuo" v-show="flag" @click="getValue()">搜索</span>
        </div>
        <transition class="hidPage" enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <div v-show="flag" class="searchpage">
                <div class="history">
                    <div class="ser">
                        <i class="iconfont icon-sousuo"></i>
                        历史搜索
                    </div>
                    <div class="del" @click="delLocal()">
                        <i class="iconfont icon-lajixiang"></i>
                    </div>
                </div>
                <div class="record"  v-if="localInputList.length==0">暂无历史搜索</div>
                <div class="record" v-else>
                    <div class="single">
                        <div v-for="(uil,i) in localInputList" :key="'uil'+i" @click="getHistory(uil)">{{uil}}</div>
                    </div>
                </div>
                <div class="hotSer">
                    <i class="iconfont icon-shoucang"></i>
                    热门搜索
                </div>
                <div class="hotlist">
                    <div v-for="(hl,i) in home.hotList" :style="`color:${hl.color}`" :key="'hl'+i" @click="getWord(hl.query)">{{hl.query}}</div>
                </div>
            </div>
        </transition>                                                 
    </div>
</template>
<script>
import { mapState,mapActions } from 'vuex';
export default {
  data() {
    return {
      flag: false,
      Flag:true,
      searchFlag:false,
      userInput:"",
      userInputList:[],
      localInputList:[]
    };
  },
  computed:{
    ...mapState(["home"])
  },
  methods: {
    getHotSearch() {
      this.flag = false;
    },
    getValue(){
        this.flag = false;
        if(!this.userInput){
          this.userInput=this.home.sketch.frontword;
        }
        this.userInputList.push(this.userInput);
        if(localStorage.getItem("userInputList")){
          let oldlist=localStorage.getItem("userInputList").split("-");
          let newlist=oldlist.concat(this.userInput);
          localStorage.setItem("userInputList",newlist.join("-"));
        }else{
          localStorage.setItem("userInputList",this.userInputList);
        }
        let list=localStorage.getItem("userInputList");
        if(list){
          this.localInputList=list.split("-");
        }
        this.$router.push({
          path:"/search",
          query:{q:this.userInput}
        });
    },
    ...mapActions(["loadhotSearch"]),
    readLocalStorage(){
      let list=localStorage.getItem("userInputList");
      if(list){
        this.localInputList=list.split("-");
      }
    },
    getWord(word){
      this.userInput=word;
    },
    delLocal(){
      localStorage.clear();
      this.localInputList=[];
    },
    getHistory(val){
      this.userInput=val;
    },
    goback(){
      this.$router.go(-1);
    }
  },
  mounted() {
    this.loadhotSearch();
    this.readLocalStorage();
  }
};
</script>
<style lang="less">
@import url("../less/less-var.less");
.topnavbar {
  &-box {
    height: @topheight; /* px */
    .search {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: @align;
      position: relative;
      .icon-style {
        font-size: @topfontheight; /* px */
        line-height: @topheight; /* px */
        flex-grow: 1;
        text-align: @align;
        color: @iconfontcolor;
      }
      .sousuo {
        font-size: 28px; /* px */
        line-height: @topheight; /* px */
        flex-grow: 1;
        text-align: @align;
        color: @iconfontcolor;
        font-family: @font-family;
      }
      .icon-sousuo {
        position: absolute;
        top: 30px; /* px */
        left: 120px; /* px */
        color: @iconfontcolor;
      }
      input {
        flex-grow: 3;
        padding-left: 50px; /* px */
        margin: @topheight*0.2 0; /* px */
        border-radius: 5px; /* px */
        border: @border @borderColor; /* no */
        outline: none;
        background-color: @bgcolor;
        font-size: @font-size; /* px */
      }
      .user-input:hover {
        border: @border @color; /* no */
        background-color: @white;
      }
    }
    .searchpage {
      position: absolute;
      top: @topheight; /* px */
      left: 0;
      bottom: 0px;
      right: 0;
      z-index: 99;
      background-color: @white;
      border-top: @border @bgcolor; /* no */
      color: @oldPriceColor;
      font-size: 28px; /* px */
      padding-left: 20px; /* px */
      padding-right: 20px; /* px */
      .history {
        height: 80px; /* px */
        line-height: 80px; /* px */
        display: flex;
        .ser {
          flex-grow: 16;
        }
        .del {
          flex-grow: 1;
        }
      }
      .record {
        margin-bottom: 35px; /* px */
        .single{
          >div{
            display: inline-block;
            border: @border @bgcolor; /* no */
            border-radius: 10px; /* px */
            padding: 10px 20px; /* px */
            margin-right: 20px; /* px */
            color: @iconfontcolor;
            margin-bottom:10px;/* px */
          }
        }
      }
      .hotSer {
        height: 80px; /* px */
        line-height: 80px; /* px */
        border-top: @border @bgcolor; /* no */
      }
      .hotlist {
        display: flex;
        flex-wrap: wrap;
        > div {
          border: @border @bgcolor; /* no */
          border-radius: 10px; /* px */
          padding: 10px 20px; /* px */
          margin-right: 20px; /* px */
          color: @iconfontcolor;
          margin-bottom:10px;/* px */
        }
      }
    }
  }
}
</style>


