import jsonpUtil from '../util/jsonpUtil';

// 定义请求地址常量
const mce_multiget="https://mce.mogucdn.com/jsonp/multiget/3";
const list_search="https://list.mogujie.com/search";
const mce_get="https://mce.mogucdn.com/jsonp/get/3";
const list_mget="https://list.mogujie.com/module/mget";
const rate_list="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";

// 通过pid请求
const pidsParams={
  appPlat:"m"
};


// 错误变量
const error=function(error){
  alert("请求错误");
};

export default {
  // 通过pid请求的方法
  loadDatasByPids(pids,success=()=>{}){
    if(!pids){
      throw new Error("请求参数错误");
    }
    if(!Array.isArray(pids)){
      pidsParams.pids=pids;
    }
    if(Array.isArray(pids)){
      let temp="";
      for (const key in pids) {
        temp += pids[key]+",";
      }
      pidsParams.pids = temp.replace(/,$/,"");
    }
    jsonpUtil.request(mce_multiget,pidsParams)
      .then((response)=>{
        if(response.ok){
          return response.json();
        }else{
          throw new Error("请求错误");
        }
      }).then(success).catch(error);
  },
  // 封装搜索相关的请求方法
  loadDatasAboutSearch(sparmas,success=()=>{}){
    if(!sparmas || sparmas.constructor !== Object){
      throw new Error("请求参数错误");
    }
    
    jsonpUtil.request(list_search,sparmas)
      .then((response)=>{
        if(response.ok){
          return response.json();
        }else{
          throw new Error("请求错误");
        }
      }).then(success).catch(error);
  },
  // 封装分类相关的请求方法
  loadDatasAboutClassify(cparmas,success=()=>{}){
    if(!cparmas || cparmas.constructor !== Object){
      throw new Error("请求参数错误");
    }
    jsonpUtil.request(mce_get,cparmas)
      .then((response)=>{
        if(response.ok){
          return response.json();
        }else{
          throw new Error("请求错误");
        }
      }).then(success).catch(error);
  },
  // 封装商品评价的请求方法
  loadCommentByItemId(comParams,success=()=>{}){
    if(!comParams || comParams.constructor !== Object){
      throw new Error("请求参数错误");
    }
    jsonpUtil.request(rate_list,comParams)
      .then((response)=>{
        if(response.ok){
          return response.json();
        }else{
          throw new Error("请求错误");
        }
      }).then(success).catch(error);
  },
  // 封装热门搜索的请求方法
  loadHotSearch(hsParams,success=()=>{}){
    if(!hsParams || hsParams.constructor !== Object){
      throw new Error("请求参数错误");
    }
    jsonpUtil.request(list_mget,hsParams)
      .then((response)=>{
        if(response.ok){
          return response.json();
        }else{
          throw new Error("请求错误");
        }
      }).then(success).catch(error);
  }
};



