import api from '../../service/api';

const state = {
  listImg:[],
  nxdyigateList:[],
  Saleslist:[],
  hotTypesList:[],
  guessLike:[],
  hotList:[],
  sketch:{}
};
const actions = {
  loadHomeData({commit}){
    api.loadDatasByPids([122224,106930,51833,51836],(data)=>{
      commit("setlistImg",data.data[122224].list);
      commit("setnxdyigateList",data.data[106930].list);
      commit("setSaleslist",data.data[51833].list);
      commit("sethotTypesList",data.data[51836].list);
    });
  },
  loadGuessLikeData({commit}){
    api.loadDatasAboutSearch({pid:"9750",sort:"pop",cKey:46},(data)=>{
      commit("setguessLike",data.result.wall.docs);
    });
  },
  loadhotSearch({commit}){
    api.loadHotSearch({code:"hotWord,sketch",platform:"H5"},(data)=>{
      commit("sethotList",data.data.hotWord.data);
      commit("setsketch",data.data.sketch.data);
    });
  }
};
const mutations = {
  setlistImg(state,data){
    state.listImg=data;
  },
  setnxdyigateList(state,data){
    state.nxdyigateList=data;
  },
  setSaleslist(state,data){
    state.Saleslist=data;
  },
  sethotTypesList(state,data){
    state.hotTypesList=data;
  },
  setguessLike(state,data){
    state.guessLike=data;
  },
  sethotList(state,data){
    state.hotList=data;
  },
  setsketch(state,data){
    state.sketch=data;
  }
};

export default {
  state,
  actions,
  mutations
};


