import jsonp from 'fetch-jsonp';

const getParamsObj=function(obj){
  let temp="?";
  for (const key in obj) {
    temp +=`${key}=${obj[key]}&`;
  }
  return temp.replace(/&$/,"");
};

export default {
  request(url,paramsObj,opt={}){
    let queryParmasObj=getParamsObj(paramsObj);
    return jsonp(url+queryParmasObj,opt);
  }
};


