<template>
<div class="detailTop-box">
  <div class="info" v-for="(pD,i) in proDetail" :key="'pD'+i">
    <div class="img">
      <img :src="pD.img" alt="">
    </div>
    <div class="title">{{pD.title}}</div>
    <div class="price">
      <span class="yen">￥</span><span class="newPrice">{{pD.price | addZero}}</span> 
      <span class="oldPrice">
        ￥{{pD.orgPrice | addZero}}
        <span class="limit">限量促销</span>
      </span>
      
    </div>
  </div>
</div>
  
</template>
<script>
  export default{
    props:{
      proDetail:{type:Array,default:[]}
    },
    methods:{
      getInfo(){
        for (const key in this.proDetail) {
          console.log(key,this.proDetail[key]);
        }
      }
    },
    mounted(){
      this.getInfo();
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .detailTop{
    &-box{
      .info{
        .img{
          height:750px;/* px */
          text-align: @align;
          img{
            height:100%;
          }
        }
        .title{
          color:@titleColor;
          font-size:32px;/* px */
          padding:10px 35px;/* px */
        }
        .price{
          padding:10px 35px;/* px */
          
          .yen{
            display: inline-block;
            vertical-align: top;
            padding-top:20px;/* px */
          }
          .newPrice{
            font-size:72px;/* px */
            vertical-align: top;
          }
          .oldPrice{
            display: inline-block;
            vertical-align: top;
            color:@oldPriceColor;
            text-decoration: line-through;
            padding-top:20px;/* px */
            position: relative;
            .limit{
              display: inline-block;
              position: absolute;
              top:50px;/* px */
              left:0px;/* px */
              color:@color;
              background:@limitBgColor;
              width:100px;/* px */
            }
          }
          
        }
      }
    }
  }
</style>


