<template>
    <div class="cartpage-box">
        <cart-top-nav-bar></cart-top-nav-bar>
        <div class="wrapper" ref="wrapper">
            <div class="content">
                
            </div>
            
        </div>
        <cart-bottom-nav-bar></cart-bottom-nav-bar>
        
    </div>
</template>
<script>
    import BScroll from "better-scroll";
    import CartTopNavBar from '../components/CartTopNavBar';
    import CartBottomNavBar from '../components/CartBottomNavBar';
    export default{
        components:{
            CartTopNavBar,CartBottomNavBar
        },
        mounted() {
            this.$nextTick(() => {
                this.scroll = new BScroll(this.$refs.wrapper, {
                    click:true
                })
            });
        }
    }
</script>
<style lang="less">
    @import url("../less/less-var.less");
    .cartpage{
        &-box{
            padding:0;
            margin:0;
            font-family: @font-family;
            .wrapper{
                position: absolute;
                left:0;
                right:0;
                top:@topheight;/* px */
                bottom:@topheight;/* px */
                overflow: hidden;
                background-color: @bodyBgColor;
            }
        }
    }
</style>


