<template>
  <div class="home-box">
    <top-nav-bar :hotList="home.hotList" :sketch="home.sketch"></top-nav-bar>
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <carousel-swiper :listImg="home.listImg"></carousel-swiper>
        <nxdyi-gate :nxdyigateList="home.nxdyigateList"></nxdyi-gate>
        <sales-promotion :Saleslist="home.Saleslist"></sales-promotion>
        <hot-types :hotTypesList="home.hotTypesList"></hot-types>
        <div class="separate"></div>
        <div class="sTitle">猜你喜欢</div>
        <guess-like :guessLike="home.guessLike"></guess-like>
      </div>
    </div>
    <bottom-nav-bar></bottom-nav-bar>
  </div>
</template>
<script>
  import TopNavBar from "../components/TopNavBar";
  import BottomNavBar from "../components/BottomNavBar";
  import BScroll from "better-scroll";
  import CarouselSwiper from '../components/CarouselSwiper';
  import NxdyiGate from '../components/NxdyiGate';
  import SalesPromotion from '../components/SalesPromotion';
  import HotTypes from "../components/HotTypes";
  import GuessLike from '../components/GuessLike';
  import api from '../service/api';

  import {mapState,mapActions,mapMutations} from 'vuex';

  export default{
    components:{
      TopNavBar,BottomNavBar,CarouselSwiper,NxdyiGate,SalesPromotion,HotTypes,GuessLike
    },
    computed:{
      ...mapState(["home"])
    },
    methods:{
      ...mapActions(["loadHomeData","loadGuessLikeData"])
    },
    mounted() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.wrapper, {
          click:true
        })
      });
      this.loadHomeData();
      this.loadGuessLikeData();
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .home{
    &-box{
      padding:0;
      margin:0;
      .wrapper{
        position: absolute;
        left:0;
        right:0;
        top:@topheight;/* px */
        bottom:@topheight;/* px */
        overflow: hidden;
        .content{
          .separate{
            background: @bodyBgColor;
            height:20px;/* px */
          }
          .sTitle{
            text-align: @align;
            background:url("https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png") no-repeat;
            background-size: 85%;
            background-position: 50px 25px;/* px */
            font-size: 30px;/* px */
            padding:15px 0;/* px */
          }
        }
      }
    }
  }
</style>


