<template>
  <div class="salesp-box">
    <div class="separate"></div>
    <div class="sTitle">超实惠-促销直达</div>
    <div class="saleList">
      <div v-for="(v,i) in Saleslist" :key="'sales'+i"  :class="'saleList'+(i+1)">
        <div class="sale-title">{{ v.title }}</div>
        <div class="sale-viceTitle">{{ v.viceTitle.replace(/[{}]/g,"") }}</div>
        <div class="sale-image">
          <img :src="v.image" alt="">
        </div>
      </div>
    </div>
    
  </div>
</template>
<script>
  export default{
    props:{
      Saleslist:{type:Array,default:[]}
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .salesp{
    &-box{
      height:600px;/* px */
      .separate{
        background: @bodyBgColor;
        height:20px;/* px */
      }
      .sTitle{
        text-align: @align;
        background:url("https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png") no-repeat;
        background-size: 85%;
        background-position: 50px 25px;/* px */
        font-size: 30px;/* px */
        padding:15px 0;/* px */
      }
      .saleList{
        .sale-title{
          color:@font-color;
          font-family: @font-family;
          font-weight: bold;
          font-size:24px;/* px */
          text-align: center;
          padding-top:20px;/* px */
        }
        .sale-viceTitle{
          color:@color;
          font-size:24px;/* px */
          text-align: center;
        }
        .saleList1{
          width:50%;
          height:280px;/* px */
          float: left;
          border-top:@border @borderColor; /* no */
          border-bottom:@border @borderColor;/* no */
          border-right:@border @borderColor;/* no */
          box-sizing: border-box;
          text-align: center;
          img{
            width:196px;/* px */
          }
        }
        .saleList2{
          width:50%;
          height:140px;/* px */
          float:left;
          border-top:@border @borderColor;/* no */
          border-bottom:@border @borderColor;/* no */
          box-sizing: border-box;
          position:relative;
          img{
            width:130px;/* px */
            position: absolute;
            right:20px;/* px */
            top:4px;/* px */
          }
          .sale-title,.sale-viceTitle{
            text-align: left;
            padding-left:20px;/* px */
          }
        }
        .saleList3{
          width:50%;
          height:140px;/* px */
          float:left;
          border-bottom:@border @borderColor;/* no */
          box-sizing: border-box;
          position:relative;
          img{
            width:130px;/* px */
            position: absolute;
            right:20px;/* px */
            top:4px;/* px */
          }
          .sale-title,.sale-viceTitle{
            text-align: left;
            padding-left:20px;/* px */
          }
        }
        .saleList4,.saleList5,.saleList6{
          width:100% / 3;
          height:230px;/* px */
          float:left;
          border-bottom:@border @borderColor;/* no */
          box-sizing: border-box;
          text-align: @align;
          img{
            width:130px;/* px */
          }
        }
        .saleList4,.saleList5{
          border-right:@border @borderColor;/* no */
        }
        
      }
    }
  }
</style>


