<template>
  <div class="guesslike-box">
    <div class="guessLike">
      <div class="single" v-for="(g,i) in guessLike" :key="'g'+i" @click="getItemId(g.tradeItemId,g)">
        <div class="production" :style="`background-image:url(${g.img})`">
          <a class="like-link"></a>
        </div>
        <div class="g-title">{{g.title}}</div>
        <div class="g-info">
          <span class="g-price">￥{{g.price}}</span>
          <span class="g-cfav">
            {{g.cfav}}
            <i class="iconfont icon-shoucang"></i>
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    props:{
      guessLike:{type:Array,default:[]},
    },
    methods:{
      getItemId(tradeItemId,g){
        localStorage.setItem(tradeItemId,JSON.stringify(g));
        this.$router.push({
          path:"/detail",
          query:{tradeItemId}
        });
      }
    },
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .guesslike{
    &-box{
      .guessLike{
        display: flex;
        flex-wrap:wrap;
        margin-top:10px;/* px */
        font-family: @font-family;
        .single{
          width:50%;
          .production{
            position: relative;
            background-size: cover;
            height:530px;/* px */
            width:94%;
            margin-left:3%;
            .like-link{
              background: url("../../static/imgs/similar.png") no-repeat 50%;
              z-index:10;
              position: absolute;
              bottom:10px; /* px */
              right:10px; /* px */
              display: block;
              width:80px;/* px */
              height:80px;/* px */
            }
          }
        }
        
        .g-title{
          text-align: left;
          height:58px;/* px */
          overflow: hidden;
          padding:0 10px;/* px */
        }
        .g-info{
          text-align: left;
          padding:15px 10px;/* px */
          margin-bottom:40px;/* px */
          .g-price{
            float:left;
            font-weight: bold;
            font-size: 28px;
          }
          .g-cfav{
            float:right;
            color:@iconfontcolor;
          }
        }
      }
    }
  }
</style>


