<template>
  <div class="searchResult-box">
    <top-nav-bar></top-nav-bar>
    <div class="sortFilter">
      <ul>
        <li v-for="(sf,i) in search.sortFilter" :class="{activeThree:i==isActive}" :key="'sf'+i"  @click="ProductSort(sf.sortKey,i)">{{sf.title}}</li>
      </ul>
    </div>
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <guess-like :guessLike="search.guessLike"></guess-like>
      </div>
    </div>
  </div>
</template>
<script>
  import BScroll from "better-scroll";
  import TopNavBar from '../components/TopNavBar';
  import GuessLike from '../components/GuessLike';
  import { mapState, mapActions } from 'vuex';
  export default{
    data(){
      return{
        isActive:0
      }
    },
    components:{
      TopNavBar,GuessLike
    },
    computed:{
      ...mapState(["search"])
    },
    methods:{
      ...mapActions(["loadSearchResult","loadDataByClassify"]),
      ProductSort(sort,i){
        if(this.$route.query.q){
          this.loadSearchResult({cKey:46,page:1,q:this.$route.query.q,sort});
        }
        if(this.$route.query.fcid){
          this.loadDataByClassify({cKey:16,page:1,sort,fcid:this.$route.query.fcid,title:this.$route.query.title});
        }
        this.isActive=i;
      }
    },
    beforeRouteUpdate (to, from, next) {
      // ...
      console.log(to);
      if(to.query.q){
        this.loadSearchResult({cKey:46,page:1,q:to.query.q,sort:"pop"});
      }
      if(to.query.fcid){
        this.loadDataByClassify({cKey:16,page:1,sort:"pop",fcid:to.query.fcid,title:to.query.title});
      }
      next();
    },
    mounted() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.wrapper, {
          click:true
        })
      });
      if(this.$route.query.q){
        this.loadSearchResult({cKey:46,page:1,q:this.$route.query.q,sort:"pop"});
      }
      if(this.$route.query.fcid){
        this.loadDataByClassify({cKey:16,page:1,sort:"pop",fcid:this.$route.query.fcid,title:this.$route.query.title});
      }
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .searchResult{
    &-box{
      padding:0;
      margin:0;
      .wrapper{
        position: absolute;
        left:0;
        right:0;
        top:@topheight + 80;/* px */
        bottom:0;/* px */
        overflow: hidden;
      }
      .sortFilter{
        height:80px;/* px */
        border-top:@border @bottomBorder;
        border-bottom:@border @bottomBorder;
        ul{
          display: flex;
          height:80px;/* px */
          :not(:last-child){
            border-right:@border @bottomBorder;
          }
          li{
            width:100%/3;
            text-align: @align;
            list-style: none;
            text-align: center;
            height:40px;/* px */
            margin-top:20px;/* px */
            vertical-align: middle;
            font-size: 28px;/* px */
          }  
          .activeThree{
            color:@color;
          }
        }
      }
    }
  }
</style>


