<template>
  <div class="classifyRight-box">
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <div class="classifyDetail">
          <div class="classifyList" v-for="(rcl,i) in RightClassifyList" :key="'rcl'+i" @click="getFcid(rcl.link,rcl.title)">
            <div class="cimg">
              <img :src="rcl.image" alt="">
            </div>
            <div>{{rcl.title}}</div>
          </div>
        </div>
        <div class="sortFilter">
          <ul>
            <li v-for="(sf,i) in sortFilter" :class="{activeTwo:i==isActive}" :key="'sf'+i"  @click="ProductSort(sf.sortKey,i)">{{sf.title}}</li>
          </ul>
        </div>
        <div class="production">
          <div class="singleprod" v-for="(pd,i) in productionList" :key="'pd'+i" v-if="pd.type==2" @click="getTradeItemId(pd.tradeItemId,pd)">
            <div class="pdimg">
              <img :src="pd.img" alt="">
              <span class="pdsale">已售{{pd.sale}}件</span>
            </div>
            <div class="pdtitle">{{pd.title}}</div>
            <div class="pdinfo">
              <span class="pdprice">￥{{pd.price}}</span>
              <span class="pdcfav">
                {{pd.cfav}}
                <i class="iconfont icon-shoucang"></i>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import BScroll from "better-scroll";
  import {mapActions,mapState} from 'vuex';
  export default{
    data(){
      return{
        sort:"",
        isActive:0,
        sale:"",
      }
    },
    props:{
      RightClassifyList:{type:Array,default:[]},
      sortFilter:{type:Array,default:[]},
      productionList:{type:Array,default:[]}
    },
    computed:{
      fcid(){
        return this.$store.state.classify.fcid;
      }
    },
    methods:{
      ProductSort(sort,i){
        this.sort=sort;
        this.loadRightClassifyProduction({page:1,sort,fcid:this.fcid});
        this.isActive=i;
      },
      ...mapActions(["loadRightClassifyProduction"]),
      getTradeItemId(tradeItemId,pd){
        localStorage.setItem(tradeItemId,JSON.stringify(pd));
        this.$router.push({
          path:"/detail",
          query:{tradeItemId}
        });
      },
      getFcid(link,title){
        let fcid=link.substring(link.indexOf("=") + 1,link.indexOf("&"));
        this.$router.push({
          path:"/search",
          query:{fcid,title}
        });
        console.log(link);
        console.log(fcid);
        console.log(title);
      }
    },
    mounted() {
        this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.wrapper, {
                click:true
            })
        });
    }
  }
</script>
<style lang="less">
  @import url("../less/less-var.less");
  .classifyRight{
    &-box{
      padding:0;
      margin:0;
      font-family: @font-family;
      .wrapper{
        position: absolute;
        left:25%;
        right:0;/* px */
        top:@topheight;/* px */
        bottom:@topheight;/* px */
        overflow: hidden;
        .content{
          .classifyDetail{
            display:flex;
            flex-wrap:wrap;
            .classifyList{
              width:100%/3;
              text-align: @align;
              margin-top:40px;/* px */
              .cimg{
                width:130px;/* px */
                text-align: @align;
                img{
                  width:100%;
                  margin-left:40px;/* px */
                }
              }
            }
          }
          .sortFilter{
            margin-top:40px;/* px */
            border-top:@border @bottomBorder;
            border-bottom:@border @bottomBorder;
            ul{
              display: flex;
              height:80px;/* px */
              :not(:last-child){
                border-right:@border @bottomBorder;
              }
              li{
                width:100%/3;
                text-align: @align;
                list-style: none;
                text-align: center;
                height:40px;/* px */
                margin-top:20px;/* px */
                vertical-align: middle;
                font-size: 28px;/* px */
              }  
              .activeTwo{
                color:@color;
              }
            }
          }
          .production{
            display: flex;
            flex-wrap: wrap;
            .singleprod{
              flex:1;
              width:50%;
              img{
                height:340px;/* px */
                margin-left:20px;/* px */
                margin-top:20px;/* px */
                width: 85%;
              }
              .pdtitle{
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                padding:10px;/* px */
              }
              .pdinfo{
                text-align: left;
                padding:15px 10px;/* px */
                margin-bottom:40px;/* px */
                .pdprice{
                  float:left;
                  font-weight: bold;
                  font-size: 28px;
                  color:@color;
                }
                .pdcfav{
                  float:right;
                  color:@iconfontcolor;
                }
              }
              .pdimg{
                position: relative;
                .pdsale{
                  width:200px;/* px */
                  height:50px;/* px */
                  line-height: 50px;/* px */
                  display: inline-block;
                  position:absolute;
                  left:22px;/* px */
                  bottom:20px;/* px */
                  background-image: url("../../static/imgs/gradient.png");
                  color:@white;
                  background-size: 100%;
                  padding-left: 10px;/* px */
                }
              }
            }
          }
        }
      }
    }
  }
</style>


